.FindJobs {
  position: fixed;
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 3fr;
  overflow: hidden;
}

.FilterPanel {
  grid-column: 1 / 2;
  background: rgb(255, 255, 255);
  padding-top: 10%;
  padding-bottom: 40%;
  padding-left: 20%;
  padding-right: 20%;
  overflow: auto;
  border-right: 1px solid rgb(225, 225, 243);
}

.Jobs {
  padding-left: 80px;
  padding-top: 50px;
  padding-bottom: 50px;
  padding-right: 20%;
  grid-column: 2 / 3;
  overflow: auto;
}

.BottomButtons {
  padding: 20px 0px;
  margin-bottom: 100px;
}
